<template>
  <div>
    <div style="height: 200vw; width: 100%; background: #2779bd; padding: 10px">
      <div style="height: 186vw; width: 100%; background: white; padding: 10px">
        <img
          src="@/static/media/icon/btn.png"
          @click="$router.push('/login')"
          style="
            height: 80px;
            width: 190px;
            position: absolute;
            right: 6vw;
            top: 0.5vw;
            padding: 10px;
          "
          alt
        />
        <p class="font-bold text-2xl font-times p-2">INFO</p>
        <hr class="style13 m-1" />
         <carousel
            :per-page="1"
            :autoplay="true"
          :autoplayTimeout="6000"
             :mouse-drag="true"
            :loop="true"
            :centerMode="true"
            :adjustableHeight="true"
            :paginationActiveColor="'transparent'"
            :speed="1000"
        >
            <slide>
              <umum />
            </slide>
            <slide>
              <pendidikan />
            </slide>
            <slide>
              <vicon />
            </slide>
        </carousel>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
import umum from '@/components/umum.vue'
import pendidikan from '@/components/pendidikan.vue'
import vicon from '@/components/vicon.vue'
import { Carousel, Slide } from 'vue-carousel';
        
        
export default {
  data() {
    return {
      mode:""
    };
  },
  components:{
    umum,
    pendidikan,
    vicon,
    Carousel,
    Slide
        
  },
  methods: {
     startTime() {
             axios.get('https://worldtimeapi.org/api/timezone/Asia/Makassar').then(res=>{
                let tanggal=new Date(res.data.datetime)
                console.log(tanggal)
                var h = tanggal.getHours();
                var m = tanggal.getMinutes();
                var s = tanggal.getSeconds();
                m = this.checkTime(m);
                s = this.checkTime(s);
                this.waktu= h + ":" + m + ":" + s;
                console.log(this.waktu)
                var t = setTimeout(this.startTime, 60000); //500 1 detik, 60000 1 menit
                if(h>=8){
                  console.log("JAM LAYANAN HABIS")
                  clearTimeout(t)
                  this.$router.push('/')
                }
            })
        },
        offlineTime() {
              let tanggal=new Date()
              console.log(tanggal)
              var h = tanggal.getHours();
              var m = tanggal.getMinutes();
              var s = tanggal.getSeconds();
              m = this.checkTime(m);
              s = this.checkTime(s);
              this.waktu= h + ":" + m + ":" + s;
              console.log(this.waktu)
              var t = setTimeout(this.startTime, 60000); //500 1 detik, 60000 1 menit
              if(h>=8){
                console.log("JAM LAYANAN HABIS")
                clearTimeout(t)
                this.$router.push('/kkulin')
              }
        },
        checkTime(i) {
            if (i < 10) {i = "0" + i};  // add zero in front of numbers < 10
            return i;
      },
       checkMode(){
      db.collection('tv-mode').onSnapshot(res=>{
        let data = res.docs.map(e=>{
          return {
            id:e.id,
            ...e.data()
          }
        })
        this.mode = data[0].mode
        console.log(data[0].mode)
        if(data[0].mode){
          this.$router.push('/')
        }
      })
    },
  },
  mounted() {
    console.log("INFO");
    this.checkMode()
    // this.startTime()
    // this.offlineTime()
  },
};
</script>