<template>
    <div>
    <div style="height:100px;"></div>
        <p class="text-center font-bold font-times text-2xl underline mt-4">INFORMASI UMUM</p>
        <div
          class="table-responsive-sm font-times p-3"
          style="box-shadow: 1px 4px 6px black; overflow: scroll"
          v-dragscroll
        >
          <!-- DEKSTOP -->
          <table
            class="table-print table-sm table-bordered table-striped table-hover table-print border-black"
            style="
            "
          >
            <thead class="mdb-color bg-black">
              <tr class="text-white">
                <th class="text-white fit text-uppercase text-2xl" >KET</th>
                <th class="text-white fit text-uppercase text-2xl" >INFO</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in datanya" :key="index+'datanya'">
                <td class="text-2xl fit">{{item.ket}}</td>
                <td class="text-2xl fit">
                    {{item.info}}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
    </div>
</template>
<script>
import firebase from 'firebase'
let db = firebase.firestore()
export default {
    data(){
        return{
            datanya:[]
        }
    },
    methods: {
        getData(){
            db.collection('tv-umum').onSnapshot(res=>{
                let data = res.docs.map(e=>{
                    return {
                        id:e.id,
                        ...e.data()
                    }
                })
                this.datanya = data
            })
        }
    },
    mounted() {
        this.getData()
    },
}
</script>
<style scoped>
td.fit, 
th.fit {
    white-space: nowrap;
    width: 1%;
}
</style>